import React from 'react';
import './../../App.css';
import Header from './../../components/header/header';
import {withRouter} from "react-router";
import {get} from 'lodash';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import {withStyles} from "@material-ui/core";
import Button from '@material-ui/core/Button';
import {Link} from 'react-router-dom';


function MainPage(props) {
  const cardContainerStyle = {
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    alignItems: 'center',
    marginTop: '50px'
  };

  const {match, classes} = props;
  const id = get(props, 'match.params.id', '');
  return (
    <div className="App">
      <Header/>
      {renderText()}
    </div>
  );

  function renderText() {
    //todo move to shared constants
    const posts = [
      {postText: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.'}
    ];
    return (
      <div className={classes.blogContainer}>
        <Paper
          className={classes.paperStyle}
        >
          {renderBackButton()}
          <Typography
            variant='h6'
          >
            {get(posts, `[${id}].postText`, '')}
          </Typography>
        </Paper>
      </div>
    );
  }

  function renderBackButton() {
    return (
      <div className={classes.buttonContainer}>
        <Link to='/'>
          <Button
            size="small"
            color="primary"
          >
            {'< Back To Main Page'}
          </Button>
        </Link>
      </div>
    );
  }
}

const styles = {
  paperStyle: {
    width: '70vw',
    margin: '40px 0',
    padding: '40px'
  },
  blogContainer: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    justifyContent: 'center'
  },
  buttonContainer: {
    display: 'flex',
    justifyContent: 'flex-start',
    position: 'relative',
    top: '-22px',
    left: '-18px',
  }
};

export default withStyles(styles)(withRouter(MainPage));
